import Slidered from "./components/sliders/Slidered";
import Sliderblue from "./components/sliders/Sliderblue";
import './App.css';
import { useState } from "react"

function App() {
  const[isSwitch, setSwitch] = useState(true);
  return (
    <div className="App">
      {isSwitch===true?<Sliderblue/>:<Slidered/>}
      <h4>Apple | Iphone 12{isSwitch===true?" Blue":" Red"}</h4>
      <p>{isSwitch===true?"Blue is sky, blue is life..":"Red is the color of love, red is warmth.."}</p>
      <div>
        <button onClick={()=>setSwitch(!isSwitch)} type="button" className="btn"></button>
      </div>
    </div>
    
  );
}

export default App;
